import React from "react";
import $ from 'jquery';
import { withPrefix } from "gatsby"
import { navigate } from "gatsby" 
import "../assets/scss/main.scss"; 
import Footer from "./Footer";
import Header from "./Header";
import blackClose from '../assets/images/black-close.svg'; 
import Helmet from 'react-helmet'

class Template extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        loading: 'is-loading'
      }
    }

    componentDidMount () {
        
       
    const isHomepage = window.location.pathname == withPrefix("/"); 
        this.timeoutId = setTimeout(() => {
            this.setState({loading: ''});
        }, 100);

        $(document).ready(function(){ 
            window.setTimeout(function(){
            $("body").css("opacity", 1);
                if(window.location.href.lastIndexOf('#') > -1){ 
                    var url = window.location.href;
                    var id = url.substring(url.lastIndexOf('#') + 1);
                    $('html, body').animate({scrollTop: $("#"+id).offset().top - 100 }, 500);
                }
            }, 500);
            
            $(window).scroll(function() {
                const scrollTop = $(this).scrollTop();
                const homepage = window.location.pathname == withPrefix("/"); 

                if (scrollTop > 100){
                    $('.scroll-top-btn').css("opacity", 1);
                    $('.scroll-top-btn').css("visibility", "visible");
                    $('.fixed-header-dark').css("opacity", 1);
                    $('.fixed-header-dark').css("visibility", "visible");
                } else {
                    $('.scroll-top-btn').css("opacity", 0);
                    $('.scroll-top-btn').css("visibility", "hidden");
                    $('.fixed-header-dark').css("opacity", 0);
                    $('.fixed-header-dark').css("visibility", "hidden");
                }

                if(!(homepage || window.location.pathname.indexOf("index.html") > -1) )return;
                else{ 
                    const footer = $('.footer-banner').offset().top - 100;
                    if (scrollTop >= footer) {
                        $('.sub-header-dark').css('display','none'); 
                    }else{
                        $('.sub-header-dark').css('display','block');
                    }
                    
                }
                
            });
            $(".scroll-top-btn").on("click", function() {
            $('html, body').animate({
                scrollTop: 0
            }, 500);
            })
            $(".nav-item").on('click', function() {
            $('.side-menu').css("left", "-100vw");
            $('body').css("overflow-y", "scroll");
            $('.ham-menu').css("opacity", "1");
            $(".main-banner").css("filter", "none");
            $(".banner").each(function() {
                $(this).css("filter", "none");
            })
            })
            $('.nav-phone').on('click', function() {
            $(".contact-modal").addClass("open-modal");
            $(".contact-modal > .modal-window").empty();
            $(".contact-modal > .modal-window").append("\
                <img class='close-button' src='"+blackClose+"'/>\
                <p class='desc'>Call us at:</p>\
                <a href='tel:62255563' class='contact'>62255563</a>\
                <p class='desc'>Whatsapp us at:</p>\
                <a href='https://api.whatsapp.com/send?phone=6581237783' class='contact'>81237783</a>\
            ")
            });
            $('.nav-email').on('click', function() {
            $(".contact-modal").addClass("open-modal");
            $(".contact-modal > .modal-window").empty();
            $(".contact-modal > .modal-window").append("\
                <img class='close-button' src='"+blackClose+"' />\
                <p class='desc'>Email any enquiries to:</p>\
                <a href='mailto:hello@madthread.com' class='contact'>hello@madthread.com</a>\
            ")
            });
            $('.nav-how').on('click', function() {  
                if(isHomepage ||  window.location.pathname.indexOf("index.html") > -1){ 
                    $('html, body').animate({
                    scrollTop: $("#how").offset().top - 100 }, 500);
                }else{
                    // window.location.href =  "/index.html#how";
                    navigate('/index.html#how')
                }
            });
            $('.nav-madSwap').on('click', function() {
                if(isHomepage ||  window.location.pathname.indexOf("index.html") > -1){ 
                    $('html, body').animate({
                    scrollTop: $("#madSwap").offset().top - 100 }, 500);
                }else{
                    // window.location.href =  "/index.html#madSwap";
                    navigate('/index.html#madSwap')

                }
            });
            $('.nav-wardrobe').on('click', function() {
                if(isHomepage ||  window.location.pathname.indexOf("index.html") > -1 ){ 
                    $('html, body').animate({
                    scrollTop: $("#wardrobe").offset().top - 100 }, 500);
                }else{
                    navigate('/index.html#wardrobe')
                }
            });
            $('.nav-testi').on('click', function() {
                if(isHomepage ||  window.location.pathname.indexOf("index.html") > -1){ 
                    $('html, body').animate({
                    scrollTop: $("#testi").offset().top - 100 }, 500);
                }else{
                    navigate('/index.html#testi')
                }
            });
            $('.nav-faq').on('click', function() {
                if(isHomepage ||  window.location.pathname.indexOf("index.html") > -1){ 
                    $('html, body').animate({
                    scrollTop: $("#faq").offset().top - 100 }, 500);
                }else{
                    navigate('/index.html#faq')
                }
            });
            $(".open-answer").on("click", function(event) {
            const questionId = event.target.attributes.answerid.value;
            const status = $(".question-" + questionId).attr("status");
            if (status == "close") {
                $(".question-" + questionId + " > .answer").css("visibility", "visible");
                $(".question-" + questionId + " > .answer").css("opacity", 1);
                $(".question-" + questionId + " > .answer").css("max-height", "1000px");
                $(".question-" + questionId).attr("status", "open");
                $(this).css("transform", "rotate(180deg)");
            } else if (status == "open") {
                $(".question-" + questionId + " > .answer").css("visibility", "hidden");
                $(".question-" + questionId + " > .answer").css("opacity", 0);
                $(".question-" + questionId + " > .answer").css("max-height", "0");
                $(".question-" + questionId).attr("status", "close");
                $(this).css("transform", "rotate(0deg)");
            }
            })
            

            $(".help-title").on("click", function(event) {
                const nextSibling = event.target.nextSibling;
                if($(nextSibling).hasClass("active")){
                    $(nextSibling).removeClass("active");
                }else{
                    $(nextSibling).addClass("active");
                }
            });

            $(".scroll-right").on("click", function(){
            const windowWidth = $(window).width()
            $('.carousel').animate({scrollLeft: '+=' + windowWidth}, 300);
            });
            $(".scroll-left").on("click", function(){
            const windowWidth = $(window).width()
            $('.carousel').animate({scrollLeft: '-=' + windowWidth}, 300);
            });
            $(".ham-menu").on("click", function(){
            $('.side-menu').css("left", "0px");
            $('body').css("overflow-y", "hidden");
            $('.ham-menu').css("opacity", "0");
            $(".banner").each(function() {
                $(this).css("filter", "blur(2px)");
            })
            });
            $(".close-side").on("click", function(){
            $('.side-menu').css("left", "-100vw");
            $('body').css("overflow-y", "scroll");
            $('.ham-menu').css("opacity", "1");
            $(".banner").each(function() {
                $(this).css("filter", "none");
            })
            });
            $(".wardrobe-thumb").on('click', function(event) {
            const brand = event.target.attributes.brand.value;
            const name = event.target.attributes.name.value;
            const price = event.target.attributes.price.value;
            const imgUrl = `url(${event.target.attributes.image.value})`;
            
            $(".wardrobe-modal").addClass("open-modal");
            $(".wardrobe-modal > .modal-window").css("background-image", imgUrl);
            $(".wardrobe-modal > .modal-window > .brand").text(brand);
            $(".wardrobe-modal > .modal-window > .name").text(name);
            $(".wardrobe-modal > .modal-window > .price > .price-val").text(price);
            });
            $(".modal-window").on("click", ".close-button", function() {
            $(".modal-container").removeClass("open-modal");
            });
            
            $(".how-join-code").on('click', function(event) {
            const title = "What’s a Join Code?";
            const content = "As soon as a spot frees up on our waitlist, a personal join code will be sent to you via email and WhatsApp. You can typically expect this within 3 days. We on-board new joining subscribers with new pieces every week, to ensure a healthy availability of apparel and customer-to-clothing ratio.";
        
            $(".pop-up").addClass("open-pop-up");
            $(".pop-up > .pop-up-container > .title").text(title);
            $(".pop-up > .pop-up-container > .content").text(content);
            });
        
            $(".how-madswap").on('click', function(event) {
        
            const title = "What’s a MadSwap?";
            const content = "Whenever you want a change of clothes, send back the styles you have on hand in exchange for something new. No waiting time in between. Just schedule for a swap on our app and we’ll come by the next day. Free shipping and dry cleaning.";
        
            $(".pop-up").addClass("open-pop-up");
            $(".pop-up > .pop-up-container > .title").text(title);
            $(".pop-up > .pop-up-container > .content").text(content);
            });
        
            $(".pop-up").on("click", ".close-button", function() {
                $(".pop-up").removeClass("open-pop-up");
            });

            $(".designers-banner .carousel-container .swiper-container .swiper-button-next").on("click", function(event) {
                $(".designers-banner .carousel-container .swiper-container .swiper-button-prev").removeClass("active"); 
                $(".designers-banner .carousel-container .swiper-container .swiper-button-next").addClass("active");
            });
            $(".designers-banner .carousel-container .swiper-container .swiper-button-prev").on("click", function(event) {
                $(".designers-banner .carousel-container .swiper-button-next").removeClass("active"); 
                $(".designers-banner .carousel-container .swiper-button-prev").addClass("active");
            });

            $(".wardrobe-banner .boardroom-carousel .swiper-container .swiper-button-next").on("click", function(event) {
                $(".wardrobe-banner .boardroom-carousel .swiper-container .swiper-button-prev").removeClass("active"); 
                $(".wardrobe-banner .boardroom-carousel .swiper-container .swiper-button-next").addClass("active");
            });
            $(".wardrobe-banner .boardroom-carousel .swiper-container .swiper-button-prev").on("click", function(event) {
                $(".wardrobe-banner .boardroom-carousel .swiper-container .swiper-button-next").removeClass("active"); 
                $(".wardrobe-banner .boardroom-carousel .swiper-container .swiper-button-prev").addClass("active");
            });

            $(".wardrobe-banner .evening-carousel .swiper-container .swiper-button-next").on("click", function(event) {
                $(".wardrobe-banner .evening-carousel .swiper-container .swiper-button-prev").removeClass("active"); 
                $(".wardrobe-banner .evening-carousel .swiper-container .swiper-button-next").addClass("active");
            });
            $(".wardrobe-banner .evening-carousel .swiper-container .swiper-button-prev").on("click", function(event) {
                $(".wardrobe-banner .evening-carousel .swiper-container .swiper-button-next").removeClass("active"); 
                $(".wardrobe-banner .evening-carousel .swiper-container .swiper-button-prev").addClass("active");
            });

            $(".wardrobe-banner .vacation-carousel .swiper-container .swiper-button-next").on("click", function(event) {
                $(".wardrobe-banner .vacation-carousel .swiper-container .swiper-button-prev").removeClass("active"); 
                $(".wardrobe-banner .vacation-carousel .swiper-container .swiper-button-next").addClass("active");
            });
            $(".wardrobe-banner .vacation-carousel .swiper-container .swiper-button-prev").on("click", function(event) {
                $(".wardrobe-banner .vacation-carousel .swiper-container .swiper-button-next").removeClass("active"); 
                $(".wardrobe-banner .vacation-carousel .swiper-container .swiper-button-prev").addClass("active");
            });
        
        }); 
    }

    componentWillUnmount () {
      if (this.timeoutId) {
          clearTimeout(this.timeoutId);
      }
    }

    render() {
        const { children } = this.props;
        return ( 
            <div className={`body ${this.state.loading}`}>  
                <Header/>
                {children}
                <Footer/>
            </div>
        );
    }
}

export default Template;
