import React from 'react'
import { Link } from 'gatsby'

class Footer extends React.Component {
    render() {
        return (
            <section id="footer">
                <div className="footer footer-nav show-desktop">
                    <div className="container">
                        <div className="nav-items">
                            <div className="grid-wrapper">
                                <div className="col-4">
                                    <a href="/ourStory" className="nav-item nav-story">Our Story</a>
                                </div>
                                <div className="col-4">
                                    <a className="nav-item nav-how">MadSwap</a>
                                </div>
                                <div className="col-4">
                                    <a className="nav-item nav-wardrobe">How It Works</a>
                                </div>
                                <div className="col-4">
                                    <a className="nav-item nav-testi">Wardrobe</a>
                                </div>
                                <div className="col-4">
                                    <a className="nav-item nav-faq">FAQ</a>
                                </div>
                            </div>
                        </div>
                        <div className="social">
                            <div className="content">
                                <div className="app-links">
                                    <p>Download Our App</p>
                                    <a className="app-btn" href="https://itunes.apple.com/sg/app/madthread/id1434370411?mt=8"><img src={require('../assets/images/apple-store.svg')}/></a>
                                    <a className="app-btn" href="https://play.google.com/store/apps/details?id=com.madthread.madthread"><img src={require('../assets/images/google-play.svg')}/></a>
                                </div>
                                <div className="social-links">  
                                    <p>Be Social</p>
                                    <div className="social-icons">
                                        <a className="nav nav-phone"><img src={require('../assets/images/phone.png')} /></a>
                                        <a className="nav nav-email"><img src={require('../assets/images/mail.png')} /></a>
                                        <a className="nav" href="https://www.instagram.com/madthreadlife/"><img src={require('../assets/images/instagram.png')} /></a>
                                        <a className="nav" href="https://www.facebook.com/madthread/"><img src={require('../assets/images/facebook.png')} /></a>
                                    </div>
                                </div>
                                <div className="sg-flag">
                                    <p>Swapping In</p>
                                    <a className="nav"><img src={require('../assets/images/sg-flag.svg') }/></a>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="footer-copyrights-policy">
                        <p className="copyrights">© 2018 MadThread Pte Ltd. All Rights Reserved. 64A Pagoda Street, Singapore 059223</p>
                        <div className="terms-policy">
                            <Link to="/terms/" replace>Terms and Conditions</Link> <span>|</span> <Link   to="/privacy/" replace>Privacy Policy</Link> 
                        </div>
                    </div>
                </div>
                <div className="footer show-mobile-flex">
                    <div className="nav-links">
                        <a className="nav-item nav-about">Our Story</a>
                        <a className="nav-item nav-how">Madswap</a>
                        <a className="nav-item nav-testi">How It Works</a>
                        <a className="nav-item nav-wardrobe">Wardrobe</a>
                        <a className="nav-item nav-faq">FAQ</a>
                    </div>
                    <div className="app-links">
                        <p>Download Our App</p>
                        <a className="app-btn" href="https://itunes.apple.com/sg/app/madthread/id1434370411?mt=8"><img src={require('../assets/images/apple-store.svg')}/></a>
                        <a className="app-btn" href="https://play.google.com/store/apps/details?id=com.madthread.madthread"><img src={require('../assets/images/google-play.svg')}/></a>
                    </div>
                    <div className="social-links">  
                        <p>Be Social</p>
                        <a className="nav nav-phone"><img src={require('../assets/images/phone.png')} /></a>
                        <a className="nav nav-email"><img src={require('../assets/images/mail.png')} /></a>
                        <a className="nav" href="https://www.instagram.com/madthreadlife/"><img src={require('../assets/images/instagram.png')} /></a>
                        <a className="nav" href="https://www.facebook.com/madthread/"><img src={require('../assets/images/facebook.png')} /></a>
                    </div>
                    <div className="sg-flag">
                        <p>Swapping In</p>
                        <a className="nav"><img src={require('../assets/images/sg-flag.svg') }/></a>
                    </div>
                    <p className="copyrights">© 2018 MadThread Pte Ltd. All Rights Reserved.<br/> 64A Pagoda Street, Singapore 059223</p>
                    <div className="terms-policy">
                        {/* <a href="/terms" >Terms and Conditions</a> <span>|</span> <a href="/privacy">Privacy Policy</a> */}
                        <Link to="/terms/" replace>Terms and Conditions</Link> <span>|</span> <Link   to="/privacy/" replace>Privacy Policy</Link> 

                    </div>
                </div>
            </section>
        )
    }
}

export default Footer
